import React from "react";
import { ButtonGroup, Button } from "@material-ui/core";

export default function SwitchButton(props) {
  const { setDeploy } = props;

  const changeStyles = (K) => {
    if (K === true) {
      let add_button = document.getElementById("add-button");
      add_button.style.backgroundColor = "#008938";

      let remove_button = document.getElementById("remove-button");
      remove_button.style.backgroundColor = "#9e9e9e";
    } else {
      let remove_button = document.getElementById("remove-button");
      remove_button.style.backgroundColor = "#008938";

      let add_button = document.getElementById("add-button");
      add_button.style.backgroundColor = "#9e9e9e";
    }
  };

  return (
    <div>
  <ButtonGroup size="large" variant="contained">
    <Button
      id="add-button"
      color="primary"
      text="white"
      onClick={() => {
        setDeploy(true);
        changeStyles(true);
      }}
      style={{
        fontFamily: "'Press Start 2P'",
        fontSize: "12px",
        maxwidth: "50%", 
      }}
    >
      Add Liquidity
    </Button>

    <Button
      id="remove-button"
      color="secondary"
      text="white"
      onClick={() => {
        setDeploy(false);
        changeStyles(false);
      }}
      style={{
        fontFamily: "'Press Start 2P'",
        fontSize: "12px", 
        maxwidth: "50%",
      }}
    >
      Remove Liquidity
    </Button>
  </ButtonGroup>
</div>


  );
}
